<template lang="pug">
  .tabs(:class="{equal}")
    .tab(
      v-for="(tab, tabIndex) in tabsFormatted"
      :key="tabIndex"
      :class="{ active: tab.isActive }"
      @click="tabClicked($event, tab.isActive, tabIndex)"
    )
      .tab-label
        slot {{ tab.label || '' }}
</template>

<script>
import createRipple from '@/utilities/createRipple'

export default {
  name: 'tabs',

  props: {
    value: {type: Number},
    equal: {type: Boolean, default: true},
    list: {type: Array, default: () => []},
  },

  data: () => ({
    tabsFormatted: [],
  }),

  mounted() {
    this.setTabs()
  },

  methods: {
    setTabs() {
      this.tabsFormatted = []
      for (const [tabIndex, tab] of this.list.entries()) {
        const tabFormatted = {isActive: tabIndex === this.value, label: tab}
        this.tabsFormatted.push(tabFormatted)
      }
    },
    tabClicked(event, isActive, index) {
      if (!isActive) createRipple(event)
      if (this.value !== index) {
        this.$emit('input', index)
        this.setTabs()
      }
    },
  },

  watch: {
    value() {
      this.setTabs()
    },
    list() {
      this.setTabs()
    },
  },
}
</script>

<style scoped lang="sass">
.tabs
  display: flex
  overflow-x: auto
  overflow-y: hidden

  &.equal > .tab
    flex-basis: 0

.tab
  flex-grow: 1
  flex-shrink: 0
  padding: 20px
  font-size: 0.85rem
  text-transform: uppercase
  font-weight: bold
  line-height: normal
  border-radius: 0 0 $radius
  border: $border-default
  border-bottom: none
  background: $white2
  cursor: pointer
  display: flex
  align-content: center
  justify-content: center
  margin: 0 2px
  color: $grey3
  transition: $transition-default
  position: relative
  overflow: hidden

  &:hover
    background: $border-default-color

  &:first-child
    margin-left: 0

  &:last-child
    margin-right: 0

  &.active
    cursor: auto
    color: $primary
    border-color: $white1
    background: $white1

    &:hover
      border-color: $white1
      background: $white1

</style>
